	module.exports = {"pathContext":{"docHtml":"<h1>fast-deep-equal</h1>\n<p>The fastest deep equal</p>\n<p><a href=\"https://travis-ci.org/epoberezkin/fast-deep-equal\"><img src=\"https://travis-ci.org/epoberezkin/fast-deep-equal.svg?branch=master\" alt=\"Build Status\"></a>\n<a href=\"http://badge.fury.io/js/fast-deep-equal\"><img src=\"https://badge.fury.io/js/fast-deep-equal.svg\" alt=\"npm version\"></a>\n<a href=\"https://coveralls.io/github/epoberezkin/fast-deep-equal?branch=master\"><img src=\"https://coveralls.io/repos/github/epoberezkin/fast-deep-equal/badge.svg?branch=master\" alt=\"Coverage Status\"></a></p>\n<h2>Install</h2>\n<pre><code class=\"language-bash\">npm install fast-deep-equal\n</code></pre>\n<h2>Features</h2>\n<ul>\n<li>ES5 compatible</li>\n<li>works in node.js (0.10+) and browsers (IE9+)</li>\n<li>checks equality of Date and RegExp objects by value.</li>\n</ul>\n<h2>Usage</h2>\n<pre><code class=\"language-javascript\">var equal = require('fast-deep-equal');\nconsole.log(equal({foo: 'bar'}, {foo: 'bar'})); // true\n</code></pre>\n<h2>Performance benchmark</h2>\n<pre><code>fast-deep-equal x 82,915 ops/sec ±0.63% (89 runs sampled)\nnano-equal x 50,506 ops/sec ±2.23% (86 runs sampled)\nshallow-equal-fuzzy x 14,873 ops/sec ±3.19% (83 runs sampled)\nunderscore.isEqual x 16,055 ops/sec ±2.29% (85 runs sampled)\nlodash.isEqual x 10,740 ops/sec ±1.04% (89 runs sampled)\ndeep-equal x 12,276 ops/sec ±2.44% (84 runs sampled)\ndeep-eql x 10,565 ops/sec ±0.89% (90 runs sampled)\nassert.deepStrictEqual x 965 ops/sec ±2.99% (81 runs sampled)\nThe fastest is fast-deep-equal\n</code></pre>\n<p>To run benchmark (requires node.js 6+):</p>\n<pre><code class=\"language-bash\">npm install\nnode benchmark\n</code></pre>\n<h2>License</h2>\n<p><a href=\"https://github.com/epoberezkin/fast-deep-equal/blob/master/LICENSE\">MIT</a></p>","docPath":"argo-ci/node_modules/fast-deep-equal/readme","proj":"argo-ci"}}

